@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&display=swap');
header {
    height: 90px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #000;
}

nav {
    width: 60%;
}

ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

li {
    list-style: none;
    color:aliceblue;
    cursor: pointer;
    font-size: 19px;
    transition: .5s;
    font-family: 'Kdam Thmor Pro', sans-serif;
}

li:hover {
    color: #D21617;
}

#logo{
    width: 150px;
    height: 90px;
}
@media (max-width: 853px){
    header{
        display: flex;
        justify-content: left;
    }
    ul{
        margin: 0 0;
        padding: 0 0;
    }
    nav{
        display: inline;
    }
    li{
        font-size: 15px;
    }
}